.topics__content {
	.topics__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 53px;
		margin-bottom: 50px;
		.topics__title {
			font-size: 35px;
			line-height: calc(52 / 35 * 100%);
			letter-spacing: 0.05em;
			color: #000000;
		}

		topics__viewall {
			a {
				font-size: 16px;
				line-height: calc(24 / 16 * 100%);
				letter-spacing: 0.05em;
				color: #6c22c9;
			}
		}
	}

	.topics__items {
		display: grid;
		column-gap: 33px;
		row-gap: 65px;
		grid-template-columns: repeat(auto-fit, minmax(376px, 1fr));
		@media (max-width: 767px) {
			grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
		}
		.topics__item {
			display: grid;
			grid-auto-flow: row;
			grid-template: auto 1fr minmax(24px, auto) / 1fr;
		}
		.topics__image {
			position: relative;
			padding-bottom: 80%;
			margin-bottom: 18px;
			& img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
		.topics__body {
			margin-bottom: 8px;
			font-weight: 500;
			font-size: 25px;
			line-height: calc(37 / 25 * 100%);
			letter-spacing: 0.05em;
			color: #000000;
		}
		.topics__footer {
			font-size: 16px;
			line-height: calc(24 / 16 * 100%);
			letter-spacing: 0.02em;
			color: #000000;
			opacity: 0.7;
			.topics__count {
			}
		}
	}
}
